h1 {
  margin: 2rem 0;
  font-size: 3.5rem;
}

.deck-form {
  width: 40%;
  margin: auto;
  margin-top: 2rem;
}

.deck-form .deck-creation {
  width: 100%;
  height: 50px;
  margin-bottom: 1.2rem;
  display: inline-block;
  position: relative;
}

.input-group {
  margin-bottom: 1.2rem;
}

.input-group input {
  padding: 0.8rem 0.5rem;
  width: 100%;
  font-size: 1.1rem;
  border: 1px solid #333;
}

.input-group input:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.deck-creation .left {
  position: absolute;
  top: 0;
  left: 0;
  width: 77%;
}

.deck-creation .right {
  position: absolute;
  top: 0;
  left: 78%;
  width: 22%;
  display: inline-block;
}

.deck-creation .right input {
  text-align: center;
}

.deck-form button {
  width: 100%;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  cursor: pointer;
  font-size: 1.1rem;
  border: none;
}

.deck-form button {
  border-radius: var(--primary-radius);
}

.deck-form button:hover {
  background-color: var(--secondary-color);
  color: white;
}

.input-group input,
.deck-form button {
  border-radius: var(--primary-radius);
}

.message{
  margin: 1rem auto -0.5rem;
  font-size: 1.1rem;
  padding: 0.5rem 0.8rem;
  border-radius: var(--primary-radius);
  width: 40%;
}

.input-group .error{
  border-color: red;
}

@media(max-width: 1300px){
  .deck-form, .message{
    width: 50%;
  }
}

@media(max-width: 1024px){
  .deck-form, .message{
    width: 60%;
  }
}

@media(max-width: 768px){
  .deck-form, .message{
    width: 70%;
  }
}

@media(max-width: 600px){
  .deck-form, .message{
    width: 90%;
  }
}
