.title {
  margin: 2rem 0;
  font-size: 3.5em;
}

.register-form {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1.2rem;
}

.register-form input {
  width: 40%;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
}

.register-form input:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.register-form button {
  width: 40%;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  cursor: pointer;
  font-size: 1.1rem;
  border: none;
}

.register-form input,
.register-form button {
  border-radius: var(--primary-radius);
}

.header a:hover {
  color: yellow;
  text-decoration: underline;
}

.register-form button:hover {
  background-color: var(--secondary-color);
  color: white;
}

.message{
  margin: -0.7rem auto 0.5rem auto;
  font-size: 1.1rem;
  border-radius: var(--primary-radius);
  text-align: left;
  color: red;
  width: 40%;
}

.input-group .error{
  border-color: red;
}

@media(max-width: 1200px){
  .register-form input, .register-form button, .message{
    width: 45%;
  }
}

@media(max-width: 1024px){
  .register-form input, .register-form button, .message{
    width: 50%;
  }

  .title{
    font-size: 3em;
    margin-bottom: 1.5rem;
  }
}

@media(max-width: 900px){
  .register-form input, .register-form button, .message{
    width: 60%;
  }
}

@media(max-width: 768px){
  .register-form input, .register-form button, .message{
    width: 65%;
  }
}

@media(max-width: 650px){
  .register-form input, .register-form button, .message{
    width: 75%;
  }

  .message{
    font-size: 1rem;
  }
}

@media(max-width: 550px){
  .register-form input, .register-form button, .message{
    width: 90%;
  }

  .title{
    font-size: 2.8em;
  }
}
