.card{
    margin-bottom: 1rem;
    text-align: left;
}

.card .number-of-result{
    background-color: black;
    color: white;
    margin-right: 0.5rem;
}

.card p{
    display: inline;
    font-size: 1.1rem;
}

.card .copy{
    color: green;
    cursor: pointer;
    margin-left: 0.1rem;
}

.card .modify{
    display: inline-block;
    margin-left: 0.5rem;
}

.card .modify div{
    display: inline-block;
    cursor: pointer;
}

.card .modify a, .card .modify div{
    padding: 0;
    margin-right: 0.3rem;
}

.card .modify a:first-of-type{
    color: blue;
}

.card .modify div{
    color: red;
}

.card .modify a:hover, .card .copy:hover, .card div:hover{
    color: purple;
}

@media(max-width: 768px){
    .card p{
        line-height: 1.5;
    }
}

@media(max-width: 600px){
    .card p{
        line-height: 1.7;
    }
}

