.info-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
  text-align: left;
}

.info-grid .deck-name {
  font-size: 1.2em;
  grid-column: 1/3;
  justify-self: start;
  margin-top: 2rem;
}

.info-grid .deck-name span {
  color: var(--primary-color);
}

.info-grid .edit-card {
  margin-left: -3rem;
  background-color: var(--primary-color);
  width: 120px;
  font-size: 1.3rem;
  border-radius: var(--primary-radius);
}

.info-grid .edit-card a {
  display: inline-block;
  color: white;
  text-decoration: none;
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  text-align: center;
  border-radius: var(--primary-radius);
}

.info-grid .edit-card a:hover {
  background-color: var(--secondary-color);
}

.info-grid .card-details {
  grid-column: 4/5;
  text-align: center;
  background-color: #eee;
  margin-left: 3rem;
  font-weight: 600;
}

.info-grid .card-details p {
  font-size: 1.1rem;
  margin-bottom: 0.6rem;
}

.info-grid .card-details .new {
  color: red;
}

.info-grid .card-details .learning {
  color: blue;
}

.info-grid .card-details .review {
  color: #00ff00;
}

@media(max-width: 1024px){
  .info-grid{
    grid-template-columns: 1fr, 150px, 1fr;
  }

  .info-grid .edit-card{
    margin-left: 0;
  }
}

@media(max-width: 900px){
  .info-grid .deck-name{
    font-size: 1.1em;
  }
}

@media(max-width: 800px){
  .info-grid{
    display: block;
  }

  .info-grid .edit-card{
    width: 60%;
    margin: auto;
  }

  .info-grid .card-details{
    margin-left: 0;
  }

  .info-grid .card-details p{
    display: inline-block;
    width: 30%;
  }

  .info-grid .deck-name{
    margin-bottom: 2rem;
  }

  .info-grid .edit-card{
    margin-bottom: 2.5rem;
  }
}

@media(max-width: 650px){
  .info-grid .edit-card{
    width: 85%;
    margin-bottom: 2rem;
  }

  .info-grid .card-details p{
    display: block;
    margin: auto;
    margin-bottom: 1rem;
  }
}