.title {
  margin: 2rem 0;
  font-size: 3.5em;
}

.add-form {
  width: 40%;
  margin: 2rem auto 3rem auto;
}

.input-group {
  margin-bottom: 1.2rem;
}

.add-form input {
  width: 100%;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
}

.add-form input:focus,
.add-form select:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.add-form button {
  width: 100%;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  cursor: pointer;
  font-size: 1.1rem;
  border: none;
}

.add-form input,
.add-form button {
  border-radius: var(--primary-radius);
}

.add-form button:hover {
  background-color: var(--secondary-color);
  color: white;
}

.add-form select {
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
  margin-bottom: 1.2rem;
  border-radius: var(--primary-radius);
  width: 100%;
}

.message {
  margin-top: -2rem;
  margin-left: -0.5rem;
  font-size: 1.1rem;
  padding: 0.5rem 0.8rem;
  border-radius: var(--primary-radius);
  text-align: left;
  color: red;
}

.error-message {
  margin-top: -1rem;
}

.input-group .error {
  border-color: red;
}

.due-info {
  font-size: 1.1rem;
  background-color: black;
  color: #00ffff;
  padding: 0.5rem 0.8rem;
  border-radius: var(--primary-radius);
  width: 40%;
  margin: -1rem auto 2rem auto;
}

@media (max-width: 1200px) {
  .add-form,
  .due-info {
    width: 45%;
  }
}

@media (max-width: 1024px) {
  .add-form {
    width: 50%;
    margin-top: 1.5rem;
  }

  .due-info {
    width: 50%;
  }

  .title {
    font-size: 3em;
    margin-bottom: 1.5rem;
  }

  .message {
    margin-top: -1rem;
  }
}

@media (max-width: 900px) {
  .add-form,
  .due-info {
    width: 60%;
  }
}

@media (max-width: 768px) {
  .add-form,
  .due-info {
    width: 65%;
  }
}

@media (max-width: 650px) {
  .add-form,
  .due-info {
    width: 75%;
  }

  .message {
    font-size: 1rem;
  }
}

@media (max-width: 550px) {
  .add-form,
  .due-info {
    width: 90%;
  }

  .title {
    font-size: 2.8em;
  }
}
