.rate-answer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 65%;
  margin: auto;
  grid-column-gap: 10px;
  margin-bottom: 2rem;
}

.rate-answer .again {
  background-color: red;
}

.rate-answer .hard {
  background-color: orangered;
}

.rate-answer .medium {
  background-color: yellow;
}

.rate-answer .easy {
  background-color: #00ff00;
}

.rate-answer p {
  margin-bottom: 3rem;
  font-size: 1.3rem;
  color: black;
  padding: 0.8rem;
  border-radius: var(--primary-radius);
  cursor: pointer;
}

.rate-answer p:hover {
  background: var(--secondary-color);
  color: white;
}

@media(max-width: 1300px){
  .rate-answer{
    width: 75%;
  }
}

@media(max-width: 1156px){
  .rate-answer{
    width: 80%;
  }
}

@media(max-width: 1050px){
  .rate-answer{
    width: 85%;
  }

  .rate-answer p{
    font-size: 1.2rem;
  }
}

@media(max-width: 950px){
  .rate-answer{
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
  }

  .rate-answer p{
    margin-bottom: 0;
  }
}

@media(max-width: 600px){
  .rate-answer{
    display: block;
  }

  .rate-answer p{
    margin-bottom: 10px;
  }
}
