.wrapper {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.status {
  color: red;
  font-size: 8em;
}

.text {
  color: red;
  font-size: 1.6em;
}

@keyframes change-color {
  0% {
    color: #e65c00;
  }

  50% {
    color: #18b201;
  }
  100% {
    color: #0456c8;
  }
}

@media (max-width: 850px) {
  .text {
    font-size: 1.5em;
  }
}

@media (max-width: 600px) {
  .wrapper {
    height: 40vh;
  }

  .status {
    font-size: 7em;
  }

  .text {
    font-size: 1.4em;
  }
}
