.info-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  margin-top: 3.5rem;
  margin-bottom: 3.5rem;
  grid-column-gap: 50px;
}

.info-grid .grid-left {
  height: 100%;
  width: 100%;
  background: url("../img/landing-img.jpg") no-repeat center center/cover;
}

h1.app-name {
  font-size: 3.5rem;
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  background: -webkit-linear-gradient(var(--secondary-color), yellow);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.info-grid .grid-right {
  text-align: left;
}

.using {
  margin-bottom: 1.5rem;
  color: var(--primary-color);
}

h2 {
  text-align: center;
}

.try {
  display: block;
  margin: 0 auto 2rem auto;
  width: 15%;
  text-align: center;
  background-color: black;
  border-radius: var(--primary-radius);
  border: none;
  outline: none;
}

.try .link {
  color: white;
  color: var(--primary-color);
  text-decoration: none;
  font-size: 1.3rem;
  display: inline-block;
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  border-radius: var(--primary-radius);
}

.header a:hover {
  color: yellow;
  text-decoration: underline;
}

.try .link:hover {
  background-color: var(--secondary-color);
  color: white;
}

@media(max-width: 1100px){
  .info-grid{
    grid-template-columns: 45% 55%;
  }

  .try{
    width: 20%;
  }
}

@media(max-width: 875px){
  .info-grid{
    display: block;
  }

  .info-grid .grid-left{
    width: 700px;
    height: 500px;
    margin: auto;
    margin-bottom: 2rem;
  }

  .try{
    width: 25%;
  }
}

@media(max-width: 768px){
  .info-grid{
    margin-bottom: 2rem;
  }

  .info-grid .grid-left{
    width: 600px;
  }
}

@media(max-width: 700px){
  .try{
    width: 100%;
  }
}

@media(max-width: 660px){
  .info-grid .grid-left{
    width: 500px;
  }
}

@media(max-width: 550px){
  .info-grid .grid-left{
    width: 400px;
  }
}

@media(max-width: 450px){
  .info-grid .grid-left{
    display: none;
  }
}