.title {
  margin: 2rem 0;
  font-size: 3.5em;
}

.user-form {
  margin: 2rem auto;
}

.input-group {
  margin-bottom: 1.2rem;
}

.user-form input {
  width: 40%;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
}

.user-form input:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.user-form button {
  width: 40%;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  cursor: pointer;
  font-size: 1.1rem;
  border: none;
}

.user-form input,
.user-form button {
  border-radius: var(--primary-radius);
}

.user-form select:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.user-form .card-number-input {
  display: inline-block;
  width: 100%;
  height: 50px;
  position: relative;
  margin-bottom: 1.2rem;
}

.user-form select {
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
  margin-bottom: 1.2rem;
  border-radius: var(--primary-radius);
  width: 77%;
  position: absolute;
  top: 0;
  left: 0;
}

.card-number-input .input-group {
  display: inline-block;
  width: 22%;
  position: absolute;
  top: 0;
  left: 78%;
}

.card-number-input .input-group input {
  text-align: center;
}

.user-form button:hover {
  background-color: var(--secondary-color);
  color: white;
}

.message{
  margin: -0.7rem auto 0.5rem auto;
  font-size: 1.1rem;
  border-radius: var(--primary-radius);
  text-align: left;
  color: red;
  width: 40%;
}

.notification{
  width: 60%;
  color: #5bc0de;
  text-align: center;
  font-size: 1.2em;
  margin: -1em auto -0.5em auto;
  background-color: black;
  padding: 0.2em 0;
  border-radius: var(--primary-radius);
  text-align: center;
  line-height: 1.4;
}

.input-group .error{
  border-color: red;
}

@media(max-width: 1256px){
  .notification{
    width: 70%;
  }
}

@media(max-width: 1200px){
  .user-form input, .user-form button, .message{
    width: 45%;
  }
}

@media(max-width: 1100px){
  .notification{
    width: 80%;
  }
}

@media(max-width: 1024px){
  .user-form input, .user-form button, .message{
    width: 50%;
  }

  .title{
    font-size: 3em;
    margin-bottom: 1.5rem;
  }

  .notification{
    margin-top: -0.5em;
  }
}

@media(max-width: 950px){
  .notification{
    width: 90%;
  }
}

@media(max-width: 900px){
  .user-form input, .user-form button, .message{
    width: 60%;
  }
}

@media(max-width: 850px){
  .notification{
    width: 100%;
  }
}

@media(max-width: 768px){
  .user-form input, .user-form button, .message{
    width: 65%;
  }
}

@media(max-width: 650px){
  .user-form input, .user-form button, .message{
    width: 75%;
  }

  .message{
    font-size: 1rem;
  }
}

@media(max-width: 550px){
  .user-form input, .user-form button, .message{
    width: 90%;
  }

  .title{
    font-size: 2.8em;
  }
}
