.header {
  background-color: black;
  display: flex;
  justify-content: space-between;
  font-size: 1.6rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: white;
}

.header .left {
  font-family: "Blaka", cursive;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.6rem;
}

.header .app-name {
  font-size: 2.6rem;
}

.header .links {
  display: inline-block;
  margin-left: 1rem;
}

.header .links a {
  display: inline-block;
}

.header .app-name,
.header .left a,
.header .right a {
  background: -webkit-linear-gradient(var(--secondary-color), yellow);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header .app-name:hover,
.header .left a:hover,
.header .right a:hover {
  background: -webkit-linear-gradient(#1e9600, #fff200, red);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header .right {
  font-family: "Blaka", cursive;
  margin-top: 0.6rem;
  margin-right: 0.6rem;
}

.header a {
  color: white;
  text-decoration: none;
  margin-left: 0.6rem;
  margin-right: 0.6rem;
}

@media (max-width: 1024px) {
  .header {
    font-size: 1.5rem;
  }

  .header .app-name {
    font-size: 2.2rem;
  }

  .header .right {
    margin-top: 0.4rem;
  }
}

@media (max-width: 950px) {
  .header .app-name {
    margin-right: 0;
  }

  .header {
    font-size: 1.4rem;
  }

  .header .app-name {
    font-size: 1.9rem;
  }

  .header .right {
    margin-top: 0.25rem;
  }
}

@media (max-width: 850px) {
  .header,
  .header .left {
    display: block;
  }

  .header .right a,
  .header .links {
    display: block;
    text-align: center;
    margin: 1rem auto;
  }

  .header .left .app-name {
    display: none;
  }

  .header a {
    font-size: 1.6rem;
  }

  .header .links a {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
}

@media (max-width: 600px) {
  .header .links a {
    display: block;
    margin: 1rem auto;
  }

  .header .links a:hover,
  .header .right a:hover {
    font-size: 2.1rem;
  }
}
