.deck {
  background-color: #000;
  padding: 5px;
  border-radius: var(--primary-radius);
}

.deck-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: flex-start;
  text-align: left;
}

.deck-grid p {
  font-size: 1.1rem;
  padding: 2px 0;
}

.deck-grid-left {
  margin-left: 0.6rem;
}

.deck-grid-right {
  text-align: center;
  width: 70%;
  justify-self: end;
  margin-right: 0.6rem;
}

.deck-name {
  color: var(--primary-color);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.new-cards {
  color: red;
}

.review-cards {
  color: #00ff00;
}

.total-cards {
  color: yellow;
}

.deck-grid-right {
  background-color: var(--primary-color);
  border-radius: var(--primary-radius);
}

.deck-grid-right a,
.operations a, .operations div {
  display: block;
  color: white;
  text-decoration: none;
  padding: 10px;
  border-radius: var(--primary-radius);
}

.deck-grid-right a:hover,
.operations a:hover,
.operations div:hover {
  background-color: var(--secondary-color);
}

.disabled {
  background-color: gray;
}

.operations {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: center;
  grid-gap: 0.5rem;
  margin: 1.5rem 0.6rem 0.4rem 0.6rem;
}

.operations div{
  cursor: pointer;
}

.operations a {
  background-color: var(--primary-color);
  background-color: white;
}

.operations .add {
  background-color: green;
}
.operations .rename {
  background-color: blue;
}

.operations .delete {
  background-color: red;
}

.operations i {
  padding-right: 8px;
}

@media(max-width: 1300px){
  .operations{
    grid-template-columns: 1fr;
    grid-gap: 0.6rem;
  }

}

@media(max-width: 1100px){
  .operations{
    grid-template-columns: repeat(3, 1fr);
  }

  .operations a{
    padding: 8px;
  }
}

@media(max-width: 900px){
  .operations{
    grid-template-columns: 1fr;
  }
}

@media(max-width: 768px){
  .deck{
    margin-bottom: 3rem;
  }

  .operations{
    grid-template-columns: repeat(3, 1fr);
  }
}

@media(max-width: 500px){
  .operations{
    display: block;
  }

  .operations a{
    margin-bottom: 0.6rem;
  }
}

















