.center {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.center-small {
  height: 60px;
}

.center-medium {
  height: 110px;
}

.center-large {
  height: 200px;
}

.ring {
  position: absolute;
  border-radius: 50%;
  animation: ring 2s linear infinite;
}

.ring-small {
  width: 60px;
  height: 60px;
}

.ring-medium {
  width: 110px;
  height: 110px;
}

.ring-large {
  width: 200px;
  height: 200px;
}

.ring::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
}

.center span {
  color: #737373;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  animation: text 3s ease-in-out infinite;
}

.center .text-medium {
  font-size: 1rem;
}

.center .text-large {
  font-size: 1.8rem;
}

@keyframes ring {
  0% {
    transform: rotate(0deg);
    box-shadow: 1px 5px 2px #e65c00;
  }

  50% {
    transform: rotate(180deg);
    box-shadow: 1px 5px 2px #18b201;
  }
  100% {
    transform: rotate(360deg);
    box-shadow: 1px 5px 2px #0456c8;
  }
}

@keyframes text {
  50% {
    color: black;
  }
}
