.card .show-answer {
    display: inline-block;
    color: white;
    text-decoration: none;
    padding: 0.5rem;
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: var(--primary-radius);
    margin-bottom: 2rem;
  }
  
  .card {
    width: 60%;
    margin: auto;
  }
  
  .card p {
    font-size: 1.5rem;
    margin: 3rem;
    text-align: justify;
    line-height: 1.3;
  }
  
  .card .show-answer {
    background-color: var(--primary-color);
    width: 160px;
    font-size: 1.3rem;
    margin-left: 2rem;
    cursor: pointer;
  }
  
  .card .show-answer:hover {
    background-color: var(--secondary-color);
  }

  @media(max-width: 1150px){
    .card{
      width: 70%;
    }
  }

  @media(max-width: 1024px){
    .card{
      width: 80%;
    }
  }

  @media(max-width: 900px){
    .card{
      width: 90%;
    }
  }

  @media(max-width: 750px){
    .card{
      width: 100%;
    }
  }

  @media(max-width: 650px){
    .card p{
      line-height: 1.4;
    }
  }