.top {
  margin-top: 2rem;
}

.top h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
}

.search input {
  width: 40%;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
  border-radius: var(--primary-radius);
  margin-right: 5px;
}

.search input:focus {
  outline: none;
  border: 2px solid var(--primary-color);
}

.search select {
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  width: 130px;
}

.search select:focus {
  border: 2px solid var(--primary-color);
  border-radius: var(--primary-radius);
  outline: none;
}

.search-result {
  margin-bottom: 3rem;
}

.search-result h2 {
  font-size: 1.3rem;
  font-weight: normal;
  margin: 1.5rem 0;
}

.search button{
  display: block;
  width: calc(40% + 135px);
  margin: auto;
  padding: 0.8rem 0.5rem;
  margin-top: 1.2rem;
  background-color: black;
  color: var(--primary-color);
  border: none;
  border-radius: var(--primary-radius);
  font-size: 1.1rem;
  cursor: pointer;
}

.search button:hover{
  background-color: var(--secondary-color);
  color: white;
}

.no-search-result {
  background-color: black;
  color: #00ff00;
  margin-top: 2.5rem;
  font-size: 1.4rem;
  padding: 0.5rem 0;
  border-radius: var(--primary-radius);
}

.copy-message{
  background-color: black;
  color: white;
  margin-top: 1rem;
  font-size: 1rem;
  padding: 0.5rem 0.8rem;
  width: calc(130px + 1.6rem);
  position: fixed;
  border-radius: var(--primary-radius);
  right: 0;
  animation: copy 2s ease-in;
}

.search .disabled{
  background-color: #333;
  color: white;
}

.search .disabled:hover{
  background-color: #333;
  cursor: no-drop;
}

.notification{
  width: 60%;
  color: white;
  text-align: center;
  font-size: 1.2em;
  margin: 0 auto 1em auto;
  background-color: red;
  padding: 0.2em 0.5em;
  border-radius: var(--primary-radius);
  text-align: center;
  line-height: 1.4;
}


@keyframes copy{
  0%{
    opacity: 0;
    bottom: 100%;
  }

  30%{
    opacity: 1;
    bottom: 0;
  }

  90%{
    opacity: 1;
    bottom: 0;
  }

  100%{
    opacity: 0.3;
    bottom: 100%;
  }
}


@media(max-width: 1256px){
  .search input{
    width: 45%;
  }

  .search button{
    width: calc(45% + 135px);
  }

  .notification{
    width: 70%;
  }
}

@media(max-width: 1100px){
  .notification{
    width: 80%;
  }
}

@media(max-width: 1024px){
  .search input{
    width: 50%;
  }

  .search button{
    width: calc(50% + 135px);
  }
}

@media(max-width: 950px){
  .notification{
    width: 90%;
  }
}

@media(max-width: 850px){
  .notification{
    width: 100%;
  }
}

@media(max-width: 768px){
  .search input{
    width: 55%;
  }

  .search button{
    width: calc(55% + 135px);
  }
}

@media(max-width: 600px){
  .search input{
    width: 65%;
  }

  .search button{
    width: calc(65% + 135px);
  }
}
