.title {
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  font-size: 3.5em;
}

.upload-info{
  margin-bottom: 2rem;
  font-size: 1.3em;
}

.upload-form {
  margin-bottom: 3rem;
  width: 40%;
  margin: auto;
}

.input-group {
  margin-bottom: 1.2rem;
}

.upload-form input {
  width: 100%;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
}

.upload-form select:focus {
  border: 2px solid var(--primary-color);
  outline: none;
}

.upload-form button {
  width: 100%;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  cursor: pointer;
  font-size: 1.1rem;
  border: none;
}

.upload-form button {
  border-radius: var(--primary-radius);
}

.upload-form button:hover,
.upload-form label:hover {
  background-color: var(--secondary-color);
  color: white;
}

.upload-form select {
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  border: 1px solid #333;
  margin-bottom: 1.2rem;
  border-radius: var(--primary-radius);
  width: 100%;
}

.upload-form {
  margin-bottom: 2rem;
}

.upload-form .input-group {
  position: relative;
  height: 50px;
}

.upload-form label {
  display: inline-block;
  background-color: black;
  color: var(--primary-color);
  padding: 0.8rem 0;
  border-radius: var(--primary-radius);
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 64%;
  font-size: 1.1rem;
  margin: 0;
}

.upload-form span {
  position: absolute;
  top: 0;
  left: 65%;
  width: 35%;
  border: 1px solid var(--secondary-color);
  padding: 0.8rem 0;
  border-radius: var(--primary-radius);
  color: var(--secondary-color);
}

.file-chosen {
  font-family: sans-serif;
}

.message{
  margin-top:  -2rem;
  margin-left: -0.5rem;
  font-size: 1.1rem;
  padding: 0.5rem 0.8rem;
  border-radius: var(--primary-radius);
  text-align: left;
  color: red;
}

.error-message{
  margin-top: -1rem;
}

.input-group .error{
  border-color: red;
}

@media(max-width: 1200px){
  .upload-form{
    width: 45%;
  }
}

@media(max-width: 1024px){
  .upload-form{
    width: 50%;
    margin-top: 1.5rem;
  }

  .title{
    font-size: 3em;
    margin-bottom: 0.5rem;
  }
}

@media(max-width: 900px){
  .upload-form{
    width: 60%;
  }
}

@media(max-width: 768px){
  .upload-form{
    width: 65%;
  }

  .upload-info{
    font-size: 1.1em;
  }
}

@media(max-width: 650px){
  .upload-form{
    width: 75%;
  }

  .message{
    font-size: 1rem;
  }
}

@media(max-width: 550px){
  .upload-form{
    width: 90%;
  }

  .title{
    font-size: 2.8em;
  }

  .upload-info{
    font-size: 1em;
  }
}
